.less-demo {
  background: white;
  margin: 12px;
  padding: 12px;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  overflow: auto;
}
.less-demo .wrap {
  background: white;
}
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 100px);
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  width: 100%;
}
